import { EntityHandlersNames } from "../../constants/entity-handlers/EntityHandlersNames";
import { AnnotationMessages } from "../../constants/messages/annotation-messages";
import { IAnnotationHandler } from "../../interfaces/entity-handler/IAnnotationHandler";
import { IEntityHandler } from "../../interfaces/entity-handler/IEntityHandler";
import { inferParamType } from "../utils/code/js/types";
import { textProcessorTools } from "../utils/text/TextProcessorTools";
import { AnnotationHandler } from "./AnnotationHandler";

const {
  eliminateKeywords,
  mergeRepeatingSymbols,
  buildParamAnnot,
  removeArrayData,
  removeObjectData,
} = textProcessorTools;

export class LambdaHandler implements IEntityHandler {
  id = EntityHandlersNames.Lambda;
  text = "";
  previousAnnotation?: IAnnotationHandler;

  constructor(text: string, previousAnnotation?: string) {
    previousAnnotation &&
      (this.previousAnnotation = new AnnotationHandler(previousAnnotation));
    this.text = text;

    this.handle = this.handle.bind(this);
  }

  private deleteOperators(str: string): string {
    return str.replace(/[.\n=>]/gim, "");
  }

  private deleteEntityFeatures(func: string): string {
    return func.replace(/[=()>{}]/gim, "");
  }

  getAnnotations(argums: string, indent: string): string[] {
    if (argums === "()") return [];
    return removeArrayData(argums)
      .split(",")
      .map((funcarg) => {
        funcarg = mergeRepeatingSymbols(funcarg, ["{", "}", "(", ")"]);
        const [paramName, paramFallback] = funcarg.split("=");
        const param = this.deleteEntityFeatures(
          this.deleteOperators(paramName)
        );
        const type = paramFallback
          ? inferParamType(
              removeObjectData(this.deleteOperators(paramFallback))
            )
          : "";
        return buildParamAnnot(indent, param, type);
      });
  }

  handle(): string {
    const funcargs = eliminateKeywords(
      this.text
        .replace(/^.*?=.*?/gi, "")
        .replace(/\s=>\s.?/gi, "")
        .replace(/ */gim, "")
    );
    const indent = this.text.match(/^ +/gi)?.[0] || "";
    if (funcargs === "()" && !this.previousAnnotation) {
      return `${indent}/** ${AnnotationMessages.Lambda} */\n${this.text}`;
    }
    if (this.previousAnnotation) {
      return this.previousAnnotation.handle(
        this.text,
        this.getAnnotations(funcargs, indent),
        indent
      );
    }
    let annotation = `${indent}/**\n${indent} * ${AnnotationMessages.Lambda}\n`;
    annotation += this.getAnnotations(funcargs, indent).join("");
    annotation += `${indent} */`;
    return `${annotation}\n${this.text}`;
  }
}
