import { Box, Code } from "@chakra-ui/react";
import { memo } from "react";
import Highlight from "react-highlight";
import { CodeInputToolbar } from "../../bars/code-input-toolbar/CodeInputToolbar";

export type CodeCanvasProps = {
  code: string;
  copyCode: VoidFunction;
  clearCanvas: VoidFunction;
};

export const CodeCanvas = memo((props: CodeCanvasProps): JSX.Element => {
  const { code, copyCode, clearCanvas } = props;
  return (
    <Box w="50%" h="100%" position="relative" p={0}>
      <Code
        h="100%"
        p={5}
        overflowY="auto"
        overflowX="hidden"
        as={Highlight}
        className="language-js"
        contentEditable
      >
        {code}
      </Code>
      <CodeInputToolbar
        onCopyButtonClick={copyCode}
        onTrashcanClick={clearCanvas}
      />
    </Box>
  );
});
