import { IAnnotationHandler } from "../../interfaces/entity-handler/IAnnotationHandler";
import { textProcessorTools } from "../utils/text/TextProcessorTools";

const { isMultiline, removeEmptyLines, replaceAndReplaceLineBreaks } =
  textProcessorTools;

export class AnnotationHandler implements IAnnotationHandler {
  annotation = "";

  constructor(annotation: string) {
    this.annotation = removeEmptyLines(annotation);
    this.handle = this.handle.bind(this);
  }

  handle(entity: string, entityAnnotations: string[], indent: string): string {
    if (!entityAnnotations.length) return `${this.annotation}\n${entity}`;
    const newEntity = entity.replace(this.annotation, "");
    let annotation = this.annotation;
    const previousAnnotationParams =
      this.annotation.match(/^ *?\* @param.+$/gm);
    previousAnnotationParams?.forEach((prevAnnotationParam) => {
      annotation = replaceAndReplaceLineBreaks(annotation, prevAnnotationParam);
      const prevParamType = prevAnnotationParam.match(/\{.*?\}/)?.[0] || "";
      const prevParamName = prevAnnotationParam
        .replace(prevParamType, "")
        .replace(/[^\w\d]+/gi, "")
        .match(/(?<=param).+?$/)?.[0];
      entityAnnotations = entityAnnotations.map((newAnnotationParam) => {
        if (prevParamName && newAnnotationParam.includes(prevParamName + " ")) {
          return prevAnnotationParam + "\n";
        }
        return newAnnotationParam;
      });
    });
    const isUniline = !isMultiline(annotation);
    if (isUniline) {
      annotation = annotation.replace(
        `${indent}/**`,
        `${indent}/**\n${indent} *`
      );
      annotation = annotation.replace(` */`, `\n${indent} */`);
    }
    annotation = removeEmptyLines(annotation);
    annotation = annotation.replace(
      `${indent} */`,
      entityAnnotations.join("") + `${indent} */`
    );
    return `${annotation}\n${newEntity}`;
  }
}
