import { IAnnotationDetector } from "../../interfaces/detectors/IAnnotationDetector";
import { textProcessorTools } from "../utils/text/TextProcessorTools";

const { escapeRegExp, lines } = textProcessorTools;

class PreviousAnnotationDetector implements IAnnotationDetector {
  detect(text: string, entity: string): string | undefined {
    //     []        ,
    //        .    .
    const annotation = text.match(
      new RegExp(
        `^ *?\\/\\*\\*[^;]+?\\*\\/ *?(?=\n${escapeRegExp(entity)})`,
        "gm"
      )
    )?.[0];
    const annotationLines = lines(annotation || "");
    let filteredAnnotationLines: string[] = [];
    annotationLines.forEach((line) => {
      if (!line.includes("*")) {
        filteredAnnotationLines = [];
      } else {
        filteredAnnotationLines.push(line);
      }
    });
    const filteredAnnotation = filteredAnnotationLines.length
      ? filteredAnnotationLines.join("\n")
      : undefined;
    return filteredAnnotation;
  }
}

export const previousAnnotationDetector = new PreviousAnnotationDetector();
