import { EntityHandlersNames } from "../../constants/entity-handlers/EntityHandlersNames";
import { IEntityDetector } from "../../interfaces/detectors/IEntityDetector";
import { IEntityHandler } from "../../interfaces/entity-handler/IEntityHandler";
import { ClassEntityHandler } from "../entity-handlers/ClassEntityHandler";
import { textProcessorTools } from "../utils/text/TextProcessorTools";
import { previousAnnotationDetector } from "./PreviousAnnotationDetector";

const excludedWords = ["constructor", "get", "set", "const", "function"];
const includedWords = ["async", "static", "static async"];

const regexes = [
  /^ *?constructor\([^%@*]*?\).*?\{(?!.*},)/gim,
  /^ *?get \w+?\(\) ?\{/gim,
  /^ *?set \w+?\([^%@*]*?\) ?\{(?!\})/gim,
  new RegExp(
    `^ *?(?!${excludedWords.join("|")})(${includedWords.join(
      "|"
    )})? \\w+\\([^%@*]*?\\).*?\\{(?!.*},)`,
    "gm"
  ),
];

export class ClassEntityDetector implements IEntityDetector {
  id = EntityHandlersNames.Class;

  detect(text: string): IEntityHandler[] {
    if (!text.match(/class \w[\w\d]+? {/)) {
      return [];
    }
    const matches: Set<string> = new Set();
    let possibleMatch: RegExpExecArray | null;
    let skip = false;
    for (const regex of regexes) {
      while ((possibleMatch = regex.exec(text))) {
        const [candidate] = possibleMatch;
        const matchesArray = [...matches];
        if (matchesArray.some((match) => match.includes(candidate))) {
          continue;
        }
        matchesArray.forEach((match, i) => {
          if (candidate.includes(match) && candidate.length > match.length) {
            skip = true;
            matchesArray[i] = candidate;
            matches.clear();
            matchesArray.forEach((match) => matches.add(match));
          }
        });
        if (skip) {
          skip = false;
        } else {
          matches.add(candidate);
        }
      }
    }
    return [...matches]
      .filter(textProcessorTools.checkIsMatchNotBanned)
      .map((match) => {
        return new ClassEntityHandler(
          match,
          previousAnnotationDetector.detect(text, match)
        );
      });
  }
}
