import {
  Box,
  Button,
  ButtonGroup,
  ChakraProvider,
  Code,
  HStack,
  Heading,
  Textarea,
  VStack,
  useToast,
} from "@chakra-ui/react";
import React, { useCallback, useEffect, useState } from "react";
import { IntlProvider, useIntl } from "react-intl";
import { JSCodeProcessor } from "./classes/text-processor/JSCodeProcessor";
import { CodeInputToolbar } from "./components/bars/code-input-toolbar/CodeInputToolbar";
import { Footer } from "./components/bars/footer/Footer";
import { CodeCanvas } from "./components/code/outputs/CodeCanvas";
import { showcaseFunction } from "./constants/misc";
import { locales } from "./resources/locales";

const processor = new JSCodeProcessor();

const stackSx = {
  pre: {
    height: "calc(100% - 50px) !important",
    padding: "0 !important",
    margin: "0 !important",
    code: {
      width: "100%",
    },
  },
};

/* 
   .
  Known issues.
 */

function App() {
  const [locale, setLocale] = useState<"en" | "ru">("en");

  return (
    <IntlProvider messages={locales[locale]} locale="en" defaultLocale="en">
      <View setLocale={setLocale} locale={locale} />
    </IntlProvider>
  );
}

export type ViewProps = {
  setLocale: (locale: "en" | "ru") => void;
  locale: "en" | "ru";
};

export const View = (props: ViewProps): JSX.Element => {
  const { locale } = props;

  const toast = useToast();
  const { formatMessage } = useIntl();
  const codeOutputWindowDefaultMessage = formatMessage({
    id: "pressGenerateAnnotations",
  });

  const [userInput, setUserInput] = useState<string>(showcaseFunction);
  const [annotation, setAnnotation] = useState<string>(
    codeOutputWindowDefaultMessage
  );

  useEffect(() => {
    setAnnotation((prev) => {
      if (prev !== codeOutputWindowDefaultMessage) {
        return formatMessage({
          id: "pressGenerateAnnotations",
        });
      }
      return prev;
    });
  }, [locale]);

  const handleUserInput = (e: React.FormEvent<HTMLTextAreaElement>): void => {
    setUserInput(e.currentTarget.value);
  };

  const annotate = (): void => {
    try {
      setAnnotation(processor.process(userInput));
    } catch (e: any) {
      console.error(e);
      setAnnotation(userInput || codeOutputWindowDefaultMessage);
    }
  };

  const clearUserCode = (): void => {
    setUserInput("");
  };

  const clearAnnotation = useCallback((): void => {
    setAnnotation(codeOutputWindowDefaultMessage);
  }, []);

  const copy = (msg: string): void => {
    try {
      navigator.clipboard.writeText(msg);
      toast({
        status: "success",
        title: formatMessage({ id: "codeCopied" }),
      });
    } catch {
      toast({
        status: "error",
        title: formatMessage({ id: "codeNotCopied" }),
      });
    }
  };

  const copyUserCode = (): void => copy(userInput);

  const copyAnnotatedCode = useCallback(
    (): void => copy(annotation),
    [annotation]
  );

  return (
    <ChakraProvider>
      <VStack h="100vh" justifyContent="space-between">
        <HStack px={10} pt={10} w="100%">
          <Heading flex={1}>Annotate.js</Heading>
        </HStack>
        <HStack px={10} h="75vh" sx={stackSx} w="100%">
          <Box w="50%" h="100%">
            <Code
              className="language-js"
              h="calc(100% - 50px)"
              p={5}
              onChange={handleUserInput}
              value={userInput}
              overflow="auto"
              as={Textarea}
              placeholder={formatMessage({ id: "yourCodeIsHere" })}
              resize="none"
            />

            <CodeInputToolbar
              onCopyButtonClick={copyUserCode}
              onTrashcanClick={clearUserCode}
            />
          </Box>
          <CodeCanvas
            code={annotation}
            copyCode={copyAnnotatedCode}
            clearCanvas={clearAnnotation}
          />
        </HStack>
        <ButtonGroup px={10} w="100%">
          <Button onClick={annotate} rounded="none">
            {formatMessage({ id: "generateAnnotations" })}
          </Button>
        </ButtonGroup>
        <Footer />
      </VStack>
    </ChakraProvider>
  );
};

export default App;
