import { IEntityDetector } from "../../interfaces/detectors/IEntityDetector";
import { IEntityHandler } from "../../interfaces/entity-handler/IEntityHandler";
import { ITextAnalyzer } from "../../interfaces/text-analyzer/ITextAnalyzer";
import { ITextProcessor } from "../../interfaces/text-processor/ITextProcessor";
import { ClassEntityDetector } from "../entity-detectors/ClassEntityDetector";
import { FunctionDetector } from "../entity-detectors/FunctionDetector";
import { LambdaDetector } from "../entity-detectors/LambdaDetector";
import { textProcessorTools } from "../utils/text/TextProcessorTools";

export class JSCodeProcessor
  implements ITextProcessor, Omit<ITextAnalyzer, "analyze">
{
  detectors: IEntityDetector[] = [
    new LambdaDetector(),
    new FunctionDetector(),
    new ClassEntityDetector(),
  ];

  private analyze(text: string): IEntityHandler[] {
    let handlers: IEntityHandler[] = [];
    for (const detector of this.detectors) {
      handlers = [...handlers, ...detector.detect(text)];
    }
    return handlers;
  }

  process(text: string): string {
    let newText = text;
    const handlers = this.analyze(text);
    for (const handler of handlers) {
      if (handler.previousAnnotation) {
        const { annotation } = handler.previousAnnotation;
        newText = textProcessorTools.replaceAndReplaceLineBreaks(
          newText,
          annotation
        );
      }
      newText = newText.replace(handler.text, handler.handle());
    }
    if (!newText) {
      throw new Error("  !    ?");
    }
    return newText;
  }
}
