import { EntityHandlersNames } from "../../constants/entity-handlers/EntityHandlersNames";
import { AnnotationMessages } from "../../constants/messages/annotation-messages";
import { IAnnotationHandler } from "../../interfaces/entity-handler/IAnnotationHandler";
import { IEntityHandler } from "../../interfaces/entity-handler/IEntityHandler";
import { inferParamType } from "../utils/code/js/types";
import { textProcessorTools } from "../utils/text/TextProcessorTools";
import { AnnotationHandler } from "./AnnotationHandler";

const {
  isMultiline,
  lines,
  buildParamAnnot,
  removeArrayData,
  hasMoreThenOneParam,
  removeObjectData,
  hasNoWords,
  concatAnnotationAndSignature,
} = textProcessorTools;

export class FunctionHandler implements IEntityHandler {
  id = EntityHandlersNames.Function;
  text = "";
  previousAnnotation?: IAnnotationHandler;

  constructor(text: string, previousAnnotation?: string) {
    previousAnnotation &&
      (this.previousAnnotation = new AnnotationHandler(previousAnnotation));
    this.text = text;

    this.handle = this.handle.bind(this);
  }

  private deleteEntityFeatures(func: string): string {
    return func.replace(/[=(){}]/gim, "");
  }

  private deletePunctuation(str: string): string {
    return str.replace(/[.\n,]/gim, "");
  }

  private formatParamData(
    paramName: string,
    paramFallback: string
  ): [string, string] {
    const param = this.deleteEntityFeatures(this.deletePunctuation(paramName));
    const type = paramFallback
      ? inferParamType(removeObjectData(this.deletePunctuation(paramFallback)))
      : "";
    return [param, type];
  }

  private handleSingleParam(indent: string, funcarg: string): string {
    const [paramName, paramFallback] = funcarg.split("=");
    if (!paramName) return "";
    const [param, type] = this.formatParamData(paramName, paramFallback);
    return buildParamAnnot(indent, param, type);
  }

  private getAnnotations(funcargs: string, indent: string): string[] {
    if (funcargs === "()") return [];
    return removeArrayData(funcargs)
      .split(",")
      .map((funcarg) => {
        return this.handleSingleParam(indent, funcarg);
      });
  }

  private getMultilineAnnotations(funcargs: string, indent: string): string[] {
    if (funcargs === "()") return [];
    const newAnnotations: string[] = lines(funcargs)
      .map((line, index) => {
        if (index === 0 && !hasMoreThenOneParam(line)) return undefined;
        if (hasMoreThenOneParam(line)) {
          return this.getAnnotations(line, indent).join("");
        } else if (!hasNoWords(line)) {
          return this.handleSingleParam(indent, line);
        }
        return undefined;
      })
      .filter(Boolean) as string[];
    return newAnnotations;
  }

  private peel(): string {
    const funcargs = this.text
      .replace(/^.*?(?=\()/gi, "")
      .replace(/(?<=\)).*?\{[\n\s]?$/gi, "")
      .replace(/ */gim, "");
    return funcargs;
  }

  handle(): string {
    const funcSignature = this.peel();
    const indent = this.text.match(/^ +/gi)?.[0] || "";
    let annotation = `${indent}/**\n${indent} * ${AnnotationMessages.Function}\n`;
    if (this.previousAnnotation) {
      const newAnnotations = this.previousAnnotation.handle(
        this.text,
        isMultiline(funcSignature)
          ? this.getMultilineAnnotations(funcSignature, indent)
          : this.getAnnotations(funcSignature, indent),
        indent
      );
      return newAnnotations;
    }
    if (isMultiline(funcSignature)) {
      annotation += this.getMultilineAnnotations(funcSignature, indent).join(
        ""
      );
      return concatAnnotationAndSignature(
        `${annotation}${indent} */`,
        this.text
      );
    }
    let [, line] = funcSignature.split(/\w+\(/gim);
    if (!line) line = funcSignature;
    if (hasNoWords(line)) {
      return `${indent}/** ${AnnotationMessages.Function} */\n${this.text}`;
    }

    annotation += this.getAnnotations(line, indent).join("");
    return concatAnnotationAndSignature(`${annotation}${indent} */`, this.text);
  }
}
